body {
  background-color: #fff;
}
.scene {
  width:600px;
  height:600px;
  margin:2% auto;
  perspective: 600px;
}
.wrapper {
  width:100%;
  height:100%;
  transform-style: preserve-3d;
  animation: box 20s linear infinite;
}
.wrapper:hover {
  -webkit-animation-play-state:paused;
  -moz-animation-play-state:paused;
  -o-animation-play-state:paused;
  animation-play-state:paused;
}

.wrapper:hover .ball .dot .border {
  -webkit-animation-play-state:paused;
  -moz-animation-play-state:paused;
  -o-animation-play-state:paused;
  animation-play-state:paused;
}
.ball {
  position: relative;
  width: 70%;
  height: 70%;
  margin: 15% auto;
  transform-style:  preserve-3d;
}
.ball .dot {
  position: absolute;
  list-style-type: none;
  top: calc(50% - 2px);
  left: calc(50% - 2px);
  transform-style:  preserve-3d;
}
.ball .dot.filled .border {
  background-color: #000;
}
.ball .dot .border {
  position: relative;
  display: block;
  width: 4px;
  height: 4px;
  border: 1px solid;
  border-radius: 50%;
  animation: mbox 20s linear infinite;
}


.ball .dot .border span {
  position: absolute;
  border: 1px solid;
  bottom: calc(100% + 5px);
  left: 2px;
  transform: translate(-50%);
  z-index: 1000;
  background: #fff;
  cursor: pointer;
  padding: 3px 8px;
  border-radius: 8px;
}

@keyframes box {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

@keyframes mbox {
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}